@import url("https://fonts.googleapis.com/css2?family=League+Spartan:wght@400;500;700&display=swap");
*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.container {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: relative;
  padding: 3.5rem 1.5rem;
}
.container .art-top {
  position: absolute;
  top: 0;
  left: 0;
}
.container .art-bottom {
  position: absolute;
  bottom: 0;
  right: 0;
}
.container .hero .details h1 {
  text-align: center;
  font: 700 2.5rem "League Spartan", sans-serif;
  line-height: 2.1rem;
  background: linear-gradient(90deg, rgb(216, 28, 28), rgb(22, 22, 222));
  padding: 1rem 0;
  background-clip: text;
  color: transparent;
}
.container .hero .details p {
  text-align: center;
  font: 500 1.1rem "League Spartan", sans-serif;
  color: hsl(303deg, 10%, 53%);
  margin: 0.5rem -0.1rem;
}
.container .hero .ratings {
  padding: 2rem 0;
}
.container .hero .ratings div {
  background: hsl(300deg, 24%, 96%);
  border-radius: 0.5rem;
  margin: 1rem 0;
  padding: 1.5rem 0;
  text-align: center;
  font: 700 0.9375rem "League Spartan", sans-serif;
  color: hsl(300deg, 43%, 22%);
}
.container .hero .ratings div img {
  width: 1.2rem;
  padding-bottom: 0.3rem;
}
.container .hero .ratings div p {
  font: 700 1rem "League Spartan", sans-serif;
  color: hsl(300deg, 43%, 22%);
}
.container .comments .card {
  background: hsl(300deg, 43%, 22%);
  margin: 1rem 0;
  border-radius: 0.5rem;
  color: hsl(0deg, 0%, 100%);
  padding: 2rem;
  min-width: 20rem;
}
.container .comments .card .person {
  display: flex;
  flex-direction: row;
  gap: 1rem;
}
.container .comments .card .person img {
  width: 2.5rem;
  height: 2.5rem;
  border-radius: 50%;
}
.container .comments .card .person .ids {
  padding-bottom: 1.5rem;
}
.container .comments .card .person .ids h2 {
  font: 700 1rem "League Spartan", sans-serif;
}
.container .comments .card .person .ids small {
  font: 500 0.9375rem "League Spartan", sans-serif;
  color: hsl(333deg, 80%, 67%);
}
.container .comments .card p {
  font: 500 1rem "League Spartan", sans-serif;
  line-height: 1.3rem;
}